export default {
	data: function() {
		return {
			pageLoad: false,
			pageData: {},
			orderid: 0,
		}
	},
	onLoad: function(ops) {
		uni.setNavigationBarTitle({
			title: "订单详情"
		})
		this.orderid = ops.orderid;
		this.getPage();
	},
	methods: {
		goShop:function(shopid){
			uni.navigateTo({
				url:"../b2b_shop/index?shopid="+shopid
			})
		},
		goGuest: function(shopid) {
			uni.navigateTo({
				url: "../b2b_guest/user?shopid=" + shopid
			})
		},
		getPage: function() {
			var that = this;
			uni.request({
				url: that.app.apiHost + "/module.php?m=b2b_order&a=show&ajax=1&orderid=" + this.orderid,
				data: {
					authcode: that.app.getAuthCode()
				},
				success: function(res) {

					that.pageData = res.data.data;
					that.pageLoad = true;
				}
			})
		},
		cancel: function() {
			var that = this;
			uni.request({
				url: that.app.apiHost + "/module.php?m=b2b_order&a=cancel&ajax=1&orderid=" + this.orderid,
				data: {
					authcode: that.app.getAuthCode()
				},
				success: function(rs) {
					that.getPage();
				}
			})
		},
		receive: function() {
			var that = this;
			uni.request({
				url: that.app.apiHost + "/module.php?m=b2b_order&a=receive&ajax=1&orderid=" + this.orderid,
				data: {
					authcode: that.app.getAuthCode()
				},
				success: function(rs) {
					that.getPage();
				}
			})
		},
		goRaty: function() {
			uni.navigateTo({
				url: "../b2b_order/raty?orderid=" + this.orderid
			})
		},
		pay: function() {
			uni.navigateTo({
				url: "../b2b_order/pay?orderid=" + this.orderid
			})
		}
	}
}
